* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: noaane;
  -ms-user-select: none;
}

.main-container {
  display: flex;
  flex-direction: row;
  justify-content: start;
}

.graphic-container {
  width: 483px;
  height: 100vh;

  position: relative;
}

.img-sfondo {
  width: 483px;
  height: 100%;
  object-fit: cover;
}

.front-card {
  width: 447px;
  position: absolute;
  top: 118px;
  left: 164px;
}

.box-data-front-card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 397px;
  height: 200px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* .box-data-front-card {
  
} */

.logo {
}

.card-number {
  color: var(--white, #fff);
  font-family: Space Grotesk;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 3.422px;
  margin-top: 20px;
}

.box-name-data {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.card-name,
.card-exp-date {
  color: var(--white, #fff);
  font-family: Space Grotesk;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.back-card {
  position: absolute;
  top: 400px;
  left: 258px;
}

.cvc {
  position: absolute;
  top: 110px;
  right: 60px;

  color: white;
  font-family: Space Grotesk;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.box-form {
  width: 100%;

  position: relative;
}

.form {
  width: 381px;

  position: absolute;
  top: 200px;
  left: 350px;

  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
}

#label-name,
#label-number-card,
#label-exp-date,
#label-cvc {
  color: var(--deep-violet, #21092f);
  font-feature-settings: "clig" off, "liga" off;
  font-family: Space Grotesk;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 10px;
  margin-top: 25px;
}

#input-card-name,
#input-card-number {
  width: 381px;
  height: 45px;
  border-radius: 8px;
  border: 1px solid var(--light-grey, #dfdee0);
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  outline: none;
  padding-left: 15px;
}

.error-number {
  color: red;
  font-size: 16px;
  border-color: red;
  border-width: 1px;
  background-color: rgba(255, 0, 0, 0.03);
}

.input-month,
.input-year {
  width: 80px;
  height: 45px;
  border-radius: 8px;
  border: 1px solid var(--light-grey, #dfdee0);
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  outline: none;
  padding-left: 15px;
}

.error-month {
  color: red;
  border: solid;
  border-color: red;
  border-width: 1px;
  background-color: rgba(255, 0, 0, 0.03);
}

.error-year {
  color: red;
  border: solid;
  border-color: red;
  border-width: 1px;
  background-color: rgba(255, 0, 0, 0.03);
}

.input-cvc {
  width: 191px;
  height: 45px;
  border-radius: 8px;
  border: 1px solid var(--light-grey, #dfdee0);
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  outline: none;
  padding-left: 15px;
}

.error-cvc {
  color: red;
  border: solid;
  border-color: red;
  border-width: 1px;
  background-color: rgba(255, 0, 0, 0.03);
}

#input-card-name::placeholder,
#input-card-number::placeholder,
#input-month::placeholder,
#input-year::placeholder,
#input-cvc::placeholder {
  color: var(--deep-violet, #c9c7cb);
  font-feature-settings: "clig" off, "liga" off;
  font-family: Space Grotesk;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  /* padding-left: 15px; */
}

.box-exp-date-cvc {
  display: flex;
  flex-direction: row;
  justify-content: start;
  gap: 20px;
}

.box-inputs-exp-date {
  display: flex;
  flex-direction: row;
  justify-content: start;
  gap: 10px;
}

.btn-confirm {
  border-radius: 8px;
  background: var(--deep-violet, #21092f);
  width: 381px;
  height: 53px;
  margin-top: 40px;

  color: #fff;
  font-feature-settings: "clig" off, "liga" off;
  font-family: Space Grotesk;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  border: none;
  -webkit-tap-highlight-color: transparent;
  outline: none;
  cursor: pointer;
}

.btn-confirm:hover {
  background-color: #3e236c;
}

.box-overlay {
  width: 381px;
  height: 500px;
  background-color: white;

  position: absolute;
  top: 200px;
  left: 350px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  /* z-index: 0;
  opacity: 0; */
}

.icon-complete {
  margin-bottom: 35px;
}

.title {
  color: #21092f;
  font-family: Space Grotesk;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 3.422px;
  margin-bottom: 16px;
}

.text {
  color: var(--purplish-grey, #8f8694);
  font-feature-settings: "clig" off, "liga" off;
  font-family: Space Grotesk;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.btn-continue {
  border-radius: 8px;
  background: var(--deep-violet, #21092f);
  width: 381px;
  height: 53px;
  margin-top: 40px;

  color: #fff;
  font-feature-settings: "clig" off, "liga" off;
  font-family: Space Grotesk;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  border: none;
  -webkit-tap-highlight-color: transparent;
  outline: none;
  cursor: pointer;
}

.btn-continue:hover {
  background-color: #3e236c;
}

