@media (max-width: 1000px) {
  .main-container {
    display: flex;
    flex-direction: column;
    justify-content: start;
  }

  .graphic-container {
    width: 100vw;
    height: auto;

    position: relative;
  }

  .img-sfondo {
    width: 100%;
    height: 330px;
    object-fit: cover;
  }

  .front-card {
    width: 447px;
    position: absolute;
    top: 170px;
    left: 50%;
    transform: translate(-60%);
    z-index: 1;
  }

  .back-card {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translate(-40%);
  }

  .box-form {
    width: 100%;

    position: relative;
  }

  .form {
    width: 381px;

    position: absolute;
    top: 200px;
    left: 50%;
    transform: translateX(-50%);
  }

  .box-overlay {
    position: absolute;
    top: 200px;
    left: 50%;
    transform: translateX(-50%);
  }
}

/* //////////////////////////////////////////////////////////////// */

@media (max-width: 600px) {
  .main-container {
    display: flex;
    flex-direction: column;
    justify-content: start;
  }

  .graphic-container {
    width: 100vw;
    height: auto;

    position: relative;
  }

  .img-sfondo {
    width: 100%;
    height: 60vw;
    object-fit: cover;
  }

  .front-card {
    width: 80vw;
    height: 45vw;
    position: absolute;
    top: 33vw;
    left: 5vw;
    transform: translate(0);
    z-index: 1;
  }

  .img-front-card {
    width: 80vw;
    height: 45vw;
  }

  .box-data-front-card {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 85%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .box-logo {
    height: 20px;
    width: 54px;
  }

  .card-number {
    font-size: 5vw;
    margin-top: 5vw;
  }

  .card-name,
  .card-exp-date {
    font-size: 3vw;
  }

  .back-card {
    position: absolute;
    top: 20px;
    left: 15vw;
    transform: translate(0);
  }

  .img-back-card {
    width: 80vw;
    height: 45vw;
  }

  .cvc {
    position: absolute;
    top: 19.5vw;
    right: 12vw;

    font-size: 4vw;
  }

  .box-form {
    width: 100%;

    position: relative;
  }

  .form {
    width: 80vw;

    position: absolute;
    top: 25vw;
    left: 50%;
    transform: translateX(-50%);
  }

  #label-name,
  #label-number-card,
  #label-exp-date,
  #label-cvc {
    font-size: 12px;
    margin-bottom: 10px;
    margin-top: 25px;
  }

  #input-card-name,
  #input-card-number {
    width: 100%;
    padding-left: 15px;
  }

  #input-month,
  #input-year {
    width: 19vw;
    padding-left: 15px;
  }

  #input-cvc {
    width: 35vw;
    padding-left: 15px;
  }

  #input-card-name::placeholder,
  #input-card-number::placeholder,
  #input-month::placeholder,
  #input-year::placeholder,
  #input-cvc::placeholder {
    font-size: 12px;
  }

  .box-exp-date-cvc {
    gap: 5vw;
  }

  .box-inputs-exp-date {
    gap: 2vw;
  }

  .btn-confirm {
    width: 80vw;
    margin-top: 40px;

    font-size: 18px;
  }

  .box-overlay {
    width: 83vw;

    position: absolute;
    top: 33vw;
    left: 50%;
    transform: translateX(-50%);

    z-index: 0;
    opacity: 0;
  }

  .title {
    font-size: 6vw;
  }

  .text {
    font-size: 15px;
  }

  .btn-continue {
    width: 80vw;
  }
}

@media (max-width: 400px) {
  #label-exp-date,
  #label-cvc {
    font-size: 3vw;
  }

  .card-number {
    font-size: 4.65vw;
  }
}

@media (max-width: 320px) {
  .card-number {
    font-size: 4.3vw;
  }
}
